import React, { useEffect, useRef }  from "react";
import Branding from "../../img/branding.png"
import Prototypes from "../../img/Mockups.png";
import Olddesign from "../../img/Olddesign.png";
import Newdesign from "../../img/Newdesign.png";
import Olddesign2 from "../../img/Olddesign2.png";
import Newdesign2 from "../../img/Newdesign2.png";
import Mockupdef from "../../img/3dmockups.png";
import { handleProjectOn } from "../../Events";
import { handleProjectOff } from "../../Events";
import { gsap } from "gsap";

const Overview = () => {
  let line1 = useRef(null);
  let line2 = useRef(null);
  useEffect(() => {
    gsap.from([line1, line2], {
      duration: 0.5,
      delay: 0.1,
      ease: "power3.out",
      opacity: 0,
      y: 44,
      stagger: {
        amount: 0.15
      }
    });
  }, [line1, line2]);
  return (
    <div>
      <div className="header-wrap">
        <div className="title-wrap">
          <h2 ref={el => (line1 = el)}>
            Redesigning Luvstay online booking platform, enabling properties to
            grow business.
          </h2>
          <h4 ref={el => (line2 = el)}>Project duration - January 2018 to October 2018</h4>
          {/* <a href="https://luvstay.com/" className="link-cta">
            See live project
          </a> */}
          <a
            href="https://web.archive.org/web/20190125015320/http://luvstay.com/"
            target="_blank"
            rel="noopener noreferrer"
            onMouseEnter={handleProjectOn}
            onMouseLeave={handleProjectOff}
            className="link-cta"
          >
            See live project
          </a>
        </div>
        <div className="image-wrap">
          <img src={Prototypes} alt="prototypes" />
        </div>
      </div>
      <div className="overview-wrap">
        <div className="overview-title">Overview</div>
        <div className="overview-description">
          <p>
            Luvstay being an online room booking platform, makes it easy for
            users to find rooms on hourly basis which are couple friendly.
            Operating across almost all major metropolitan cities in India,
            Luvstay caters from adults to individuals.
          </p>
          <p>
            Luvstay developed <a href="https://luvstay.com/">luvstay.com</a> as
            their proprietary website, allowing users the facility of using
            their local hotel rooms, anonymously (without document submissions).
            Facilities like online anonymous booking, hourly basis rates,
            uplifted Luvstays' service to new level amongst competition.
          </p>
          <p>
            I led the redesign of luvstay.com. Within the front-end team, having
            a keen eye for design, I was praised for going beyond and designing
            a site from scratch before development.
          </p>
        </div>
      </div>
      <div className="three-card-section">
        <div className="wrap">
          <div className="card card-one">
            <span>Problem</span>
            <p>
              In this digital age, when users across globe are moving to mobile
              devices, Luvstays' platform was not responsive. Globally, 68
              percent of website visits are done on mobile as users prefer handy
              devices over desktops. For business to flourish, it is inevident
              to make Luvstay a mobile-friendly website. The website had unclear
              navigation and required rebranding.{" "}
            </p>
          </div>
          <div className="card card-two">
            <span>Solution</span>
            <p>
              Luvstay board of directors gave me an opportunity to overhaul the
              brand and website experience from scratch to improve on user
              interface and responsiveness while being in-line with their core
              values of booking rooms for loved ones, safely, securely and
              anonymously.
            </p>
          </div>
          <div className="card card-three">
            <span>My role</span>
            <p>
              My main role was to lead the redesign of website and brand,
              improving user experience in conjunction with development team and
              handle cross-functional issues. By improving the experience across
              devices, 36% increase in traffic and 15% increase in conversion
              rate from mobile/tablet was established.
            </p>
          </div>
        </div>
        <span>
          Note: Due to a non-disclosure agreement with the client, I cannot
          display the design process details for this project. However following
          are few design solutions which were pushed to the production and the
          impact of my work.
        </span>
      </div>
      <div className="comparision-section">
        <div className="old-section">
          <span>Existing design</span>
          <div>
            <img src={Olddesign} alt="prototype" />
          </div>
          <div>
            <img src={Olddesign2} alt="prototype" />
          </div>
        </div>
        <div className="gray-wrap">
          <span>Redesigned platform</span>
          <div className="new-section">
            <div>
              <img src={Newdesign} alt="prototype" />
            </div>
            <div>
              <img src={Newdesign2} alt="prototype" />
            </div>
          </div>
        </div>
      </div>
      <div className="text-section">
        <span>Strategy</span>
        <p>
        The Board concurred usage of current website to be primary user portal for booking and hosting enquiry services, over a dedicated application, given the same website on redesigning will be resized for all types of devices and orientations and provide users an optimal user experience at scale.
        </p>
      </div>
      <div className="text-section">
        <span>Goals</span>
        <div>
        Primary goals are as follows:
        <ul>
            <li>Rebranding platform, with new color scheme, typography and iconography.</li>
            <li>Clarification of navigation.</li>
            <li>Responsiveness across all types of devices for optimal user viewing support.</li>
            <li>Improve conversion rates.</li>
        </ul>
        </div>
      </div>
      <div className="text-section">
        <span>Rebranding</span>
        <p>
        Luvstay caters to the need of individuals for hourly stay rates. We created a solution, keeping Luvstaysâ€™ core values intact in each step during rebranding. From new color scheme and typography to logo designs and iconography; user research points(using social media and internet) were taken into account and feedback was analysed to create an experience reflecting the core values of Luvstay giving it a new visual style and clear navigation. Now Luvstay can cater to the potential host and customers seemlessly through a unique simple online platform.
        </p>
        
          <img className="logo-image" src={Branding} alt="logo transformation"/>
        
        <p>The new brand synergises with the ethics of this business, while being in a bold persona than before, and makes a clear statement to target audience, all along with a soothing color schema.</p>
      </div>
      <div className="text-section">
        <span>Results</span>
        <p>
        Today, Luvstay comes as a major competition for brief stay booking platform in India. The company maintains its presence on current customers while its online services banks on a well-thought strategic and technical foundation. Rebranding indicated for new user and property acquisition from various demographics across India increasing business growth.
        </p>
      </div>
      <div className="text-section">
        <span>Outcomes</span>
        <div>
        A responsive web experience made successful impacts on the Luvstaysâ€™ buisness and sales growth with stunning results:
        <ul>
            <li>Improved Google Search Results ranking resulting increase in organic traffic by 36%.</li>
            <li>Improved mobile/tablet user conversion rates to 27%.</li>
            <li>Improved Google Mobile usability automated auditing score from 46/100 to 77/100.</li>
        </ul>
        I feel great to have lead a team for this redesign.
        </div>
      </div>
      <div className="text-section">
        <span>What have I learned</span>
        <div>
        A responsive web experience made successful impacts on the Luvstaysâ€™ buisness and sales growth with stunning results:
        <ul>
            <li><b>Importance of clear navigation</b> across webpages delivers a stark message to a customer regarding the product. Meeting customer needs with present technology limitations can be done with using an appropriate user journey, within service experience to deliver seemless results.

            </li>
            <li><b>Details in design process</b> articulates naunces which takes user understanding, analysis of user feedback, feature prototyping and user testing, into account. This detailed approach creates solutions for complex user problems.</li>
            <li><b>Understanding of design and development hurdles</b> and communicating the same to stakeholders, project managers and developers. Depending on the level or position, clear and smooth communication of ideas and deliverables is a must for the best results overall.</li>
        </ul>
        </div>
      </div>
      <div className="project-prototypes">
        <div>
          <img src={Mockupdef} alt="prototype" />
        </div>
      </div>
    </div>
  );
};

export default Overview;
