

export const handleReactHover = () => {
    document.querySelector(".text-react").classList.add("active")
}

export const handleReactNoHover = () => {
    document.querySelector(".text-react").classList.remove("active")
}
export const handleNodeHover = () => {
    document.querySelector(".text-node").classList.add("active")
}

export const handleNodeNoHover = () => {
    document.querySelector(".text-node").classList.remove("active")
}
export const handleMailOn = () => {
    document.querySelector(".shadow-game").classList.add("fill")
}
export const handleMailOff = () => {
    document.querySelector(".shadow-game").classList.remove("fill")
}
export const handleLinkOn = () => {
    document.querySelector(".shadow-game-la").classList.add("fill-link")
}
export const handleLinkOff = () => {
    document.querySelector(".shadow-game-la").classList.remove("fill-link")
}
export const handleMediumOn = () => {
    document.querySelector(".shadow-game-medium").classList.add("fill-m")
}
export const handleMediumOff = () => {
    document.querySelector(".shadow-game-medium").classList.remove("fill-m")
}
export const handleProjectOn = () => {
    document.querySelector(".link-cta").classList.add("fill-m")
}
export const handleProjectOff = () => {
    document.querySelector(".link-cta").classList.remove("fill-m")
}

