import React from 'react'

const Day = () => {
    const arr = ["SUNDAY", "MONDAY", "TUESDAY", "WEDNESDAY", "THURSDAY", "FRIDAY", "SATURDAY"]
    var today = new Date();
    var currentDay = today.getDay();
    return (arr[currentDay])
}

