import gsap from "gsap";

// OPEN MENU
export const staggerReveal = (node1, node2) => {
  gsap.from([node1, node2], {
    duration: 0.4,
    height: 0,
    transformOrigin: "right top",
    skewY: 2,
    ease: "power3.inOut",
    stagger: {
      amount: 0.1
    }
  });
};

// CLOSE MENU
export const staggerRevealClose = (node1, node2) => {
  gsap.to([node1, node2], {
    duration: 0.4,
    height: 0,
    ease: "power3.inOut",
    stagger: {
      amount: 0.07
    }
  });
};

// STAGGER THE LINKS TO APPEAR
export const staggerText = (node1, node2, node3) => {
  gsap.from([node1, node2, node3], {
    duration: 0.4,
    y: 100,
    delay: 0.1,
    ease: "power3.inOut",
    stagger: {
      amount: 0.3
    }
  });
};

// Fade up for the additonal info on our menu
export const fadeInUp = node => {
  gsap.from(node, {
    y: 60,
    duration: 0.5,
    delay: 0.2,
    opacity: 0,
    ease: "power3.inOut"
  });
};

// Hover on the link
export const handleHover = e => {
  gsap.to(e.target, {
    duration: 0.1,
    y: 3,
    skewX: 4,
    ease: "power1.inOut"
  });
};

// Hover off the link
export const handleHoverExit = e => {
  gsap.to(e.target, {
    duration: 0.1,
    y: -3,
    skewX: 0,
    ease: "power1.inOut"
  });
};

// adds city image once you hover on
export const handleCity = (city, target) => {
  console.log(city)
  if(city === '/portfolio-v1/static/media/heaven.b9b6815c.png'){
    document.getElementsByClassName("info-gen")[0].style.display = "none";
    document.getElementsByClassName("info-luv")[0].style.display = "none";
    document.getElementsByClassName("info-sparklin")[0].style.display = "none";
    document.getElementsByClassName("info-td")[0].style.display = "none";
    document.getElementsByClassName("info-hb")[0].style.display = "block";
    document.getElementById("nav").style.visibility = "hidden";
    
  }else if(city === '/portfolio-v1/static/media/luvstay.6c27f442.png'){
    document.getElementsByClassName("info-gen")[0].style.display = "none";
    document.getElementsByClassName("info-hb")[0].style.display = "none";
    document.getElementsByClassName("info-sparklin")[0].style.display = "none";
    document.getElementsByClassName("info-td")[0].style.display = "none";
    document.getElementsByClassName("info-luv")[0].style.display = "block";
    document.getElementById("nav").style.visibility = "hidden";
  }else if(city === '/portfolio-v1/static/media/newyork.63129058.jpg'){
    document.getElementsByClassName("info-gen")[0].style.display = "none";
    document.getElementsByClassName("info-hb")[0].style.display = "none";
    document.getElementsByClassName("info-luv")[0].style.display = "none";
    document.getElementsByClassName("info-td")[0].style.display = "none";
    document.getElementsByClassName("info-sparklin")[0].style.display = "block";
    document.getElementById("nav").style.visibility = "hidden";
  }
  else if(city === '/portfolio-v1/static/media/beijing.209dcd10.webp'){
    document.getElementsByClassName("info-gen")[0].style.display = "none";
    document.getElementsByClassName("info-hb")[0].style.display = "none";
    document.getElementsByClassName("info-luv")[0].style.display = "none";
    document.getElementsByClassName("info-sparklin")[0].style.display = "none";
    document.getElementsByClassName("info-td")[0].style.display = "block";
    document.getElementById("nav").style.visibility = "hidden";
  }
  // document.getElementsByClassName("info")[0].style.filter = "blur(1.5rem)";
  gsap.to(target, {
    duration: 0,
    background: `url(${city}) center center`
  });
  gsap.to(target, {
    duration: 0.4,
    opacity: 1,
    ease: "power3.inOut"
  });
  gsap.from(target, {
    duration: 0.4,
    skewY: 2,
    transformOrigin: "right top"
  });
};

// Removes the city image once you hover off
export const handleCityReturn = target => {
  document.getElementsByClassName("info-hb")[0].style.display = "none";
  document.getElementsByClassName("info-luv")[0].style.display = "none";
  document.getElementsByClassName("info-sparklin")[0].style.display = "none";
  document.getElementsByClassName("info-td")[0].style.display = "none";
  document.getElementsByClassName("info-gen")[0].style.display = "block";
  document.getElementById("nav").style.visibility = "visible";
  gsap.to(target, {
    duration: 0,
    skewY: 0
  });
  gsap.to(target, {
    duration: 0.4,
    opacity: 0,
    skewY: 0
  });
};
