import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import Luvstay from "./docs/Luvstay/Luvstay";
import HeavensBowl from "./docs/Heavensbowl/Heavensbowl";
import { HashRouter, Route } from "react-router-dom";
import "./AppStyles.scss";

ReactDOM.render(
    <>
    <HashRouter basename="/">
            <Route path="/" exact component={App} />
            <Route path="/luvstay" exact component={Luvstay} />
            <Route path="/heavensbowl" exact component={HeavensBowl} />
    </HashRouter>
    </>
    , document.getElementById('root'));


