import React, { useState, useEffect } from "react";
import download from "../img/download.png";
import { withRouter, Link } from "react-router-dom";
import Resume from "../resume/Nischal.pdf";
import Hamburger from "./Ham";
import DarkMode from "../Dark";



const Header = ({ history }) => {

  const [state, setState] = useState({
    initial: false,
    clicked: null,
    menuName: "About",
  });

  const [disabled, setDisabled] = useState(false);
  const [hide, setHide] = useState(false);

  //Use Effect
  useEffect(() => {
    //Listening for page changes.
    history.listen(() => {
      setState({ clicked: false, menuName: "About" });
    });
    if (window.location.hash !== "#/") {
      setHide(true);
    }
  }, [history]);
 

  //Determine if out menu button should be disabled
  const disableMenu = () => {

      setDisabled(!disabled);
      setTimeout(() => {
        setDisabled(false);
      }, 1200);
    };
    
    const handleMenu = () => {
      
      disableMenu();
      document.body.style.overflow = 'hidden';
      if (state.initial === false) {
      
      setState({
        initial: null,
        clicked: true,
        menuName: "About",
      });
    } else if (state.clicked === true) {
      
      setState({
        clicked: !state.clicked,
        menuName: "About",
      });
    } else if (state.clicked === false) {
      
      setState({
        clicked: !state.clicked,
        menuName: "About",
      });
    }
  };

  

  return (
    
    <header>
      <div className="header-wrapper">
        <div className="header-blur">
          <div className="logo-wrapper">
            <div>
              <Link className="logo" to="/">
                <svg
                  width="297"
                  height="199"
                  viewBox="0 0 297 199"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                  stroke-width="5"
                    d="M71.4666 88.5455V199H0.898438V2.6364H67.8871V39.9659H69.9325C74.1939 27.5228 81.6939 17.7642 92.4325 10.6904C103.256 3.61652 115.87 0.0795898 130.273 0.0795898C144.165 0.0795898 156.225 3.27561 166.452 9.66765C176.765 15.9745 184.734 24.6676 190.359 35.7472C196.069 46.8267 198.881 59.483 198.796 73.716V199H128.228V88.5455C128.313 78.8296 125.842 71.2017 120.813 65.662C115.87 60.1222 108.967 57.3523 100.103 57.3523C94.3075 57.3523 89.2365 58.6307 84.8899 61.1875C80.6286 63.6591 77.3473 67.2387 75.0462 71.9262C72.745 76.5284 71.5518 82.0682 71.4666 88.5455Z"
                    fill="black"
                  />
                  <path
                    className="dot"
                    stroke-width="5"
                    d="M297.001 155.5C297.001 179.524 277.525 199 253.501 199C229.476 199 210.001 179.524 210.001 155.5C210.001 131.476 229.476 112 253.501 112C277.525 112 297.001 131.476 297.001 155.5Z"
                    fill="black"
                  />
                </svg>
              </Link>
            </div>
          </div>
          <div className="ul-wrapper">
            <ul className="unordered">
              <li>
              <DarkMode />
                        </li>
              <li style={{ display: hide ? "none" : "flex" }}>
                <div
                  className="header-tags"
                  id="about-me"
                  disabled={disabled}
                  onClick={handleMenu}
                  
                >
                  {state.menuName}
                  <svg
                    width="24"
                    height="24"
                    viewBox="0 0 24 24"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M5 21H19C20.103 21 21 20.103 21 19V5C21 3.897 20.103 3 19 3H5C3.897 3 3 3.897 3 5V19C3 20.103 3.897 21 5 21ZM5 5H19L19.001 19H5V5Z"
                      fill="black"
                    />
                    <path
                      d="M12 13.586L7.70697 9.29297L6.29297 10.707L12 16.414L17.707 10.707L16.293 9.29297L12 13.586Z"
                      fill="black"
                    />
                  </svg>
                </div>
              </li>
            
              <li>
                <a
                  className="header-tags"
                  target="__blank"
                  rel="noopener noreferrer"
                  href="https://github.com/nickb94"
                >
                  Github
                </a>
              </li>
              
              <li>
                <a className="header-tags" href={Resume} download>
                  Resume{" "}
                  <img
                    alt="dowload-icon"
                    className="down-load"
                    src={download}
                  ></img>
                </a>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <Hamburger state={state} />
    </header>
  );
};

export default withRouter(Header);
